import React, { useState } from "react";

function StudentListItem(props) {
  const [isBtnDisbaled, setIsBtnDisbaled] = useState(false);

  const fullname = props.fullname ? props.fullname : " ";
  console.log(props.fullname, typeof fullname);
  return (
    <tr>
      <td>
        <img
          src={`${process.env.REACT_APP_URL}/uploads/user_image/` + props.image}
          alt={props.fullname}
          title={props.fullname}
        />
      </td>
      <td>{props.phone_number}</td>
      <td>{fullname}</td>

      <td>
        <b className={`text-${props.status ? "danger" : "success"}`}>
          {props.status ? " " : " "}
        </b>
      </td>
      <td>
        <button
          className="btn btn-primary pt-2 hint--top mx-1"
          aria-label=""
          onClick={() => props.updateItem(props.phone_number)}
        >
          
          <i className="icon-pencil px-1"></i>
        </button>
        <button
          className={`btn btn-${
            props.status ? "success" : "danger"
          } pt-2 hint--top mx-1`}
          aria-label=""
          disabled={isBtnDisbaled}
          onClick={async () => {
            setIsBtnDisbaled(true);
            setIsBtnDisbaled(await props.updateBlockItem(props.phone_number));
          }}
        >
          {props.status ? "" : ""}
          {props.status ? (
            <i className="icon-plus px-1"></i>
          ) : (
            <i className="icon-trash px-1"></i>
          )}
        </button>
      </td>
    </tr>
  );
}

export default StudentListItem;
