import React from "react";

export default function index({
  hasMore,
  setHasMore,
  itemCount,
  pageCount,
  currentPage,
  setCurrentPage,
}) {
  if (pageCount < currentPage) setCurrentPage(pageCount);
  return (
    <>
      <ul className="pagination pagination-sm mx-auto">
        <li className="page-item">
          <button
            className="page-link mx-1"
            tabIndex=""
            onClick={(e) => {
              setCurrentPage(currentPage - 1);
            }}
            disabled={currentPage == 1 ? true : false}
          >
            {/* {status === "active" */}
            <span className="sr-only">()</span>
            
          </button>
        </li>
        {currentPage == 1 ? null : (
          <li className="page-item">
            <button
              className="page-link"
              tabIndex={currentPage - 1}
              onClick={(e) => {
                setCurrentPage(currentPage - 1);
              }}
            >
              {currentPage - 1}
            </button>
          </li>
        )}
        <li className="page-item active">
          <button className="page-link" tabIndex={currentPage}>
            {currentPage}
          </button>
        </li>

        {currentPage == pageCount ? null : (
          <li className="page-item">
            <button
              className="page-link"
              tabIndex={currentPage + 1}
              onClick={(e) => {
                setHasMore(false);
                setCurrentPage(currentPage + 1);
              }}
            >
              {currentPage + 1}
            </button>
          </li>
        )}

        <li className="page-item mx-1">
          <button
            className="page-link"
            onClick={(e) => {
              setHasMore(false);
              setCurrentPage(currentPage + 1);
            }}
            tabIndex=""
            disabled={hasMore ? false : true}
          >
            
          </button>
        </li>
      </ul>
      <p className="my-1">
        <b>  : </b> {`${itemCount}`}
      </p>
    </>
  );
}
