import React, { useState, useEffect } from "react";
import axios from "axios";
import StudentListItem from "./StudentListItem";
import ModalUpdateStudent from "../ModalUpdateStudent";
import Pagination from "../../../shared/components/Pagination";
import { notificationAlert } from "../../../../../utils/shared";

import {
  ErrorStatus,
  ErrorMessage,
  Show,
} from "../../../../../services/Recoils";
import { useRecoilState } from "recoil";
function StudentList() {
  const [usersData, setUsersData] = useState([]);

  const [modalState, setModalState] = useState(false);

  const [phone_number, setPhone_number] = useState("");
  const [reference_phone_number, setReference_phone_number] = useState("");
  const [name, setName] = useState("");
  const [lastname, setLastname] = useState("");
  const [name_english, setName_english] = useState("");
  const [lastname_english, setLastname_english] = useState("");
  const [father_name, setFather_name] = useState("");
  const [grade, setGrade] = useState();
  const [school, setSchool] = useState("");
  const [province, setProvince] = useState("");
  const [city, setCity] = useState("");
  const [user_image, setUser_image] = useState("default_image.png");

  const [hasMore, setHasMore] = useState(false);
  const [itemCount, setItemCount] = useState(0);
  const [pageCount, setPageCount] = useState(1);
  const [currentPage, setCurrentPage] = useState(1);

  const [errorStatus, setErrorStatus] = useRecoilState(ErrorStatus);
  const [errorMessage, setErrorMessage] = useRecoilState(ErrorMessage);
  const [show, setshow] = useRecoilState(Show);

  useEffect(() => {
    updateListComponent();
  }, [currentPage]);

  const updateItem = (phone_number) => {
    const list = usersData.filter(
      (item, index) => item.phone_number == phone_number
    );
    console.log(list);
    setPhone_number(list[0].phone_number);
    setReference_phone_number(list[0].reference_phone_number);
    setName(list[0].name);
    setLastname(list[0].lastname);
    setName_english(list[0].name_english);
    setLastname_english(list[0].lastname_english);
    setFather_name(list[0].father_name);
    setGrade(list[0].grade);
    setSchool(list[0].school);
    setProvince(list[0].province);
    setCity(list[0].city);
    setUser_image(list[0].user_image);

    setModalState(true);
  };

  const updateListComponent = () => {
    let data = new FormData();
    data.append("page", currentPage);
    axios({
      url:
        process.env.REACT_APP_BACKEND_URL + process.env.REACT_APP_GET_ALL_USERS,
      withCredentials: true,
      method: "POST",
      data,
    }).then((res) => {
      try {
        console.log(res.data.data);
        setHasMore(res.data.data.hasMore);
        setItemCount(res.data.data.itemCount);
        setPageCount(res.data.data.pageCount);
        setUsersData(res.data.data.data ? res.data.data.data : []);
      } catch (e) {
        //you could show there res.data.message here for example
      }
    });
  };

  const updateBlockSingleUser = (phone_number) => {
    let data = new FormData();
    data.append("phone_number", phone_number);
    return axios({
      url:
        process.env.REACT_APP_BACKEND_URL +
        process.env.REACT_APP_ADMIN_UPDATE_USER_BLOCK,
      withCredentials: true,
      method: "POST",
      data,
    })
      .then((res) => {
        console.log(res.data);

        updateListComponent();
        setErrorStatus(res.data.success ? "success" : "danger");
        setErrorMessage(res.data.message);

        notificationAlert(
          errorStatus === "danger" ? " !" : " !",
          { errorMessage },
          errorStatus === "danger" ? "error" : "success"
        );
      })
      .finally(() => {
        return false;
      });
  };

  return (
    <>
      <div className="container-fluid">
        <section className="wrapper">
          <h2 className="section-title">   </h2>
          <div className="container-fluid">
            <table className="table table-bordered table-striped text-center">
              <thead className="thead-light">
                <tr>
                  <th scope="col"></th>
                  <th scope="col"> </th>
                  <th scope="col">   </th>
                  <th scope="col"></th>
                  <th scope="col"> / </th>
                </tr>
              </thead>
              <tbody>
                {usersData.map((item, index) => (
                  <StudentListItem
                    key={index}
                    image={item.user_image}
                    fullname={item.name + " " + item.lastname}
                    father_name={item.father_name}
                    role={item.role}
                    status={item.status}
                    phone_number={item.phone_number}
                    updateItem={updateItem}
                    updateBlockItem={updateBlockSingleUser}
                  />
                ))}
              </tbody>
            </table>

            <nav aria-label="...">
              <Pagination
                hasMore={hasMore}
                setHasMore={setHasMore}
                itemCount={itemCount}
                pageCount={pageCount}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
              />
            </nav>
          </div>
        </section>
      </div>
      <ModalUpdateStudent
        modalState={modalState}
        setModalState={setModalState}
        setErrorStatus={setErrorStatus}
        setErrorMessage={setErrorMessage}
        updateListComponent={updateListComponent}
        phone_number={phone_number}
        setPhone_number={setPhone_number}
        reference_phone_number={reference_phone_number}
        setReference_phone_number={setReference_phone_number}
        name={name}
        setName={setName}
        lastname={lastname}
        setLastname={setLastname}
        name_english={name_english}
        setName_english={setName_english}
        lastname_english={lastname_english}
        setLastname_english={setLastname_english}
        father_name={father_name}
        setFather_name={setFather_name}
        grade={grade}
        setGrade={setGrade}
        school={school}
        setSchool={setSchool}
        province={province}
        setProvince={setProvince}
        city={city}
        setCity={setCity}
        user_image={user_image}
        setUser_image={setUser_image}
        setShow={setshow}
      />
    </>
  );
}

export default StudentList;
