import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import HomePage from "../componenets/pages/public/HomePage";
import SignIn from "../componenets/pages/public/SignIn";

import StudentUserInfo from "../componenets/pages/student/StudentUserInfo";

import AdminUserList from "../componenets/pages/admin/AdminUserList";

import NotFoundPage from "../componenets/partials/shared/components/NotFoundPage";

import AdminPrivateRoute from "../utils/AdminPrivateRoute";
import StudentPrivateRoute from "../utils/StudentPrivateRoute";
import SigninPrivateRoute from "../utils/SigninPrivateRoute";

export default function MainRouter() {
  return (
    <Switch>
      {/* Public routes */}
      {/* index */}
      <Route
        exact
        path="/"
        name="HomePage"
        render={(props) => {
          return <HomePage {...props} />;
        }}
      />

      {/* STUDENT ROUTES */}
      <Route
        exact
        path="/student/userinfo"
        name="StudentUserInfo"
        render={(props) => (
          <StudentPrivateRoute>
            <StudentUserInfo {...props} />
          </StudentPrivateRoute>
        )}
      />

      {/* Userlist for admin */}
      <Route
        exact
        path="/admin/userlist"
        name="AdminUserList"
        render={(props) => (
          <AdminPrivateRoute>
            <AdminUserList {...props} />
          </AdminPrivateRoute>
        )}
      />

      {/* Login Route */}
      <Route
        exact
        path="/signin"
        name="SignIn"
        render={(props) => (
          <SigninPrivateRoute>
            <SignIn {...props} />
          </SigninPrivateRoute>
        )}
      />

      <Route
        name=" "
        render={(props) => {
          return (
            <NotFoundPage
              title=" ...!"
              text="     "
              mound_text="404"
              link="/"
              link_text="  "
              {...props}
            />
          );
        }}
      />
    </Switch>
  );
}
