import React, { useState, useEffect } from "react";
import Province from "../../../shared/components/province.json";
import CompleteList from "../../../shared/components/completeList.json";
import Axios from "axios";
import { ErrorStatus, ErrorMessage } from "../../../../../services/Recoils";
import { useRecoilState } from "recoil";
// import AlertItem from "../../../public/components/Alert/AlertItem";
import { notificationAlert } from "../../../../../utils/shared";

export default function StudentInfoForm() {
  const [Cities, setCities] = useState([]);
  const [file, setFile] = useState("");
  const [isLoaded, setIsLoaded] = useState(false);
  const [phone_number, setPhone_number] = useState("");
  const [reference_phone_number, setReference_phone_number] = useState("");
  const [name, setName] = useState("");
  const [lastname, setLastname] = useState("");
  const [name_english, setName_english] = useState("");
  const [lastname_english, setLastname_english] = useState("");
  const [father_name, setFather_name] = useState("");
  const [grade, setGrade] = useState("");
  const [school, setSchool] = useState("");
  const [province, setProvince] = useState("");
  const [city, setCity] = useState("");
  const [user_image, setUser_image] = useState("default_image.png");
  const [disabledButton, setDisabledButton] = useState(false);

  useEffect(() => {
    updateListComponent();
  }, []);

  const handleProvinceOnChange = (e) => {
    e.preventDefault();

    var theProvince = e.target.options[e.target.selectedIndex].value;

    let tempArray = CompleteList.filter((item) => {
      return item.province == theProvince;
    });
    tempArray = tempArray.map((item) => {
      return item.state;
    });
    tempArray = [...new Set([...tempArray])];
    setProvince(theProvince);
    setCities(tempArray);
  };

  const handleAvatarOnChange = (e) => {
    setFile(e.target.files[0]);
    //User ref instead in this component
    document.querySelector("#custom-text-avatar").innerHTML = "";
    document
      .querySelector("#custom-button-avatar")
      .classList.add("btn-warning");
    // console.log(e.target.files[0], e.target.files[0].name);
  };

  const handleFormOnSubmit = (e) => {
    e.preventDefault();
    setDisabledButton(true);

    let data = new FormData();

    data.append("phone_number", phone_number);
    data.append("reference_phone_number", reference_phone_number);
    data.append("name", name);
    data.append("lastname", lastname);
    data.append("name_english", name_english);
    data.append("lastname_english", lastname_english);
    data.append("father_name", father_name);
    data.append("grade", grade);
    data.append("school", school);
    data.append("province", province);
    data.append("city", city);
    data.append("image", file);

    for (var key of data.entries()) {
      console.log(key[0] + ", " + key[1]);
    }
    Axios({
      url:
        process.env.REACT_APP_BACKEND_URL +
        process.env.REACT_APP_UPDATE_USER_INFORMATION,
      withCredentials: true,
      headers: {
        "Content-Type": "multipart/form-data",
      },
      method: "POST",
      data: data,
    })
      .then((res) => {
        console.log(res.data);
        notificationAlert(
          res.data.success ? " !" : " !",
          res.data.message,
          res.data.success ? "success" : "error"
        );
        if (res.data.success) updateListComponent();
      })
      .finally(() => {
        setDisabledButton(false);
      });
  };

  const updateListComponent = async () => {
    Axios({
      url:
        process.env.REACT_APP_BACKEND_URL +
        process.env.REACT_APP_GET_USER_INFORMATION,
      withCredentials: true,
      method: "POST",
    }).then((res) => {
      // console.log(res);

      try {
        setPhone_number(res.data.data.phone_number);
        setReference_phone_number(res.data.data.reference_phone_number);
        setName(res.data.data.name);
        setLastname(res.data.data.lastname);
        setName_english(res.data.data.name_english);
        setLastname_english(res.data.data.lastname_english);
        setFather_name(res.data.data.father_name);
        setGrade(res.data.data.grade);
        setSchool(res.data.data.school);
        setProvince(res.data.data.province);
        //get the cities list too
        let tempArray = CompleteList.filter((item) => {
          return item.province == res.data.data.province;
        });
        tempArray = tempArray.map((item) => {
          return item.state;
        });
        tempArray = [...new Set([...tempArray])];
        setCities(tempArray);
        setCity(res.data.data.city);
        setUser_image(res.data.data.user_image);
      } catch (e) {
        //show alert that user is not conneted or login or redirect, your choice
      }
    });
  };

  return (
    <section className="form-wrapper has-icon wrapper">
      <h2 className="section-title">  </h2>
      <div className="container-fluid">
        <form className="row" onSubmit={handleFormOnSubmit}>
          <div className="col-md-6 col-lg-4 form-group">
            <label htmlFor="phoneNumber">
               <i className="icon-people"></i>
            </label>
            <input
              className="form-control"
              type="text"
              name="phone_number"
              id="phoneNumber"
              value={phone_number}
              placeholder=" "
              readOnly
            />
          </div>

          <div className="col-md-6 col-lg-4 form-group">
            <label htmlFor="referencePhoneNumber">
                <i className="icon-people"></i>
            </label>
            <input
              className="form-control"
              type="text"
              name="reference-phone-number"
              id="referencePhoneNumber"
              value={reference_phone_number}
              placeholder=" "
              readOnly
            />
          </div>

          <div className="col-md-6 form-group">
            <label htmlFor="name">
               <i className="icon-user"></i>
            </label>
            <input
              className="form-control"
              type="text"
              name="name"
              id="name"
              placeholder=" "
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
          </div>

          <div className="col-md-6 form-group">
            <label htmlFor="lastname">
                <i className="icon-user-follow"></i>
            </label>
            <input
              className="form-control"
              type="text"
              name="lastname"
              id="lastname"
              placeholder="  "
              value={lastname}
              onChange={(e) => setLastname(e.target.value)}
            />
          </div>

          <div className="col-md-6 form-group">
            <label htmlFor="name-eng">
                 <i className="icon-user"></i>
            </label>
            <input
              className="form-control"
              type="text"
              name="name-eng"
              id="name-eng"
              placeholder="Your Name"
              value={name_english}
              onChange={(e) => setName_english(e.target.value)}
            />
          </div>

          <div className="col-md-6 form-group">
            <label htmlFor="lastname-eng">
                  <i className="icon-user-follow"></i>
            </label>
            <input
              className="form-control"
              type="text"
              name="lastname-eng"
              id="lastname-eng"
              placeholder="Your lastname"
              value={lastname_english}
              onChange={(e) => setLastname_english(e.target.value)}
            />
          </div>

          <div className="col-md-6 col-lg-4 form-group">
            <label htmlFor="fatherName">
               <i className="icon-people"></i>
            </label>
            <input
              className="form-control"
              type="text"
              name="father-name"
              id="fatherName"
              placeholder="  "
              value={father_name}
              onChange={(e) => setFather_name(e.target.value)}
            />
          </div>

          <div className="col-md-6 col-lg-4 form-group">
            <label htmlFor="grade">
               <i className="icon-badge"></i>
            </label>
            <select
              name="grade"
              id="grade"
              className="form-control"
              value={grade}
              onChange={(e) => setGrade(e.target.value)}
            >
              <option value="0"> </option>
              <option value="1"> </option>
              <option value="2"> </option>
              <option value="3"> </option>
              <option value="4"> </option>
              <option value="5"> </option>
            </select>
          </div>

          <div className="col-md-6 col-lg-4 form-group">
            <label htmlFor="schoolName">
                <i className="icon-graduation"></i>
            </label>
            <input
              className="form-control"
              type="text"
              name="school"
              id="schoolName"
              placeholder="  "
              value={school}
              onChange={(e) => setSchool(e.target.value)}
            />
          </div>

          <div className="col-md-6 col-lg-4 form-group">
            <label htmlFor="province">
              <i className="icon-map"></i>
            </label>
            <select
              name="province"
              id="province"
              className="form-control"
              onChange={handleProvinceOnChange}
              value={province}
            >
              <option defaultValue> </option>

              {Province.map((item, index) => (
                <option key={index} value={item}>
                  {item}
                </option>
              ))}
            </select>
          </div>

          <div className="col-md-6 col-lg-4 form-group">
            <label htmlFor="city">
              <i className="icon-cursor"></i>
            </label>
            <select
              name="city"
              id="city"
              className="form-control"
              value={city}
              onChange={(e) => setCity(e.target.value)}
            >
              <option value="0"> </option>
              {Cities.map((item, index) => (
                <option key={index} value={item}>
                  {item}
                </option>
              ))}
            </select>
          </div>

          <div className="col-12 col-lg-6 form-group">
            <label htmlFor="avatar"> </label>
            <img
              src={
                `${process.env.REACT_APP_IMAGE_URL}/uploads/user_image/` +
                user_image
              }
              className="d-block mb-2"
              alt=" "
              width="170"
              height="200"
            />
            <div>
              <input
                type="file"
                className="btn btn-primary"
                data-ripple="ripple"
                id="custom-button-avatar"
                onChange={handleAvatarOnChange}
              />

              <div id="custom-text-avatar">    </div>
            </div>
          </div>

          <div className="col-12 form-group mt-5">
            <div>
              <input
                type="submit"
                className="btn btn-success"
                data-ripple="ripple"
                id="custom-button-submit"
                value=" "
                disabled={disabledButton}
              />
            </div>
          </div>
        </form>
      </div>
    </section>
  );
}
