import React from "react";
import "../../../../../assets/public/css/main/_rankingWrapper.scss";
import PropTypes from "prop-types";
import slider_image1 from "../../../../../assets/public/images/student01.jpg";
import slider_image2 from "../../../../../assets/public/images/student02.jpg";
import RankingWrapperItems from "./RankingWrapperItems";
import RankingWrapperSliderItems from "./RankingWrapperSliderItems";
import Carousel from "react-elastic-carousel";
function RankingWrapper({ ...props }) {
  const breakPoints = [
    { width: 1, itemsToShow: 1 },
    { width: 550, itemsToShow: 2 },
    { width: 850, itemsToShow: 2 },
    { width: 1150, itemsToShow: 2 },
    { width: 1450, itemsToShow: 2 },
    { width: 1750, itemsToShow: 2 },
  ];
  return (
    <section className="ranking-wrapper wrapper">
      <div className="container">
        <div className="row">
          <div className="col-md-7">
            <div className="title-box">
              <span>{props.title_box}</span>
            </div>
            <h4 className="title">{props.title}</h4>
            <h6 className="desc">{props.desc}</h6>
            <p>{props.text}</p>
            <ul>
              <RankingWrapperItems
                title=""
                text="        "
              ></RankingWrapperItems>
              <RankingWrapperItems
                title=" "
                text="        "
              ></RankingWrapperItems>
              <RankingWrapperItems
                title=""
                text="        "
              ></RankingWrapperItems>
            </ul>
          </div>

          <div className="col-md-5">
            <div className="best-title">
              <span>{props.slider_title}</span>
            </div>
            <div className="ranking-carousel owl-carousel owl-theme">
              <Carousel
                className="elastic-carousel"
                itemsToShow={2}
                itemPadding={[5, 5]}
                breakPoints={breakPoints}
                pagination={false}
              >
                <RankingWrapperSliderItems
                  image={slider_image1}
                  user_name="  "
                  text="    "
                ></RankingWrapperSliderItems>
                <RankingWrapperSliderItems
                  image={slider_image2}
                  user_name="  "
                  text="    "
                ></RankingWrapperSliderItems>
                <RankingWrapperSliderItems
                  image={slider_image1}
                  user_name="  "
                  text="    "
                ></RankingWrapperSliderItems>
              </Carousel>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

RankingWrapper.propTypes = {
  title_box: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  desc: PropTypes.string.isRequired,
  text: PropTypes.string.isRequired,
  slider_title: PropTypes.string.isRequired,
};
export default RankingWrapper;
