import React from "react";
import "../../../../../assets/public/css/main/_teacherWrapper.scss";
import TeacherItemList from "./TeacherItemList";
import teacher01 from "../../../../../assets/public/images/teacher01.jpg";
import teacher02 from "../../../../../assets/public/images/teacher02.jpg";
import teacher03 from "../../../../../assets/public/images/teacher03.jpg";
import Carousel from "react-elastic-carousel";
import PropTypes from "prop-types";
function TeacherWrapper({ ...props }) {
  const breakPoints = [
    { width: 1, itemsToShow: 1 },
    { width: 150, itemsToShow: 1 },
    { width: 400, itemsToShow: 2 },
    { width: 850, itemsToShow: 3 },
    { width: 1150, itemsToShow: 4 },
    { width: 1450, itemsToShow: 4 },
    { width: 1750, itemsToShow: 4 },
  ];

  return (
    <section className="teacher-wrapper wrapper">
      <div className="container">
        <div className="title-box">
          <h2 className="title">
            <span>{props.title}</span>
          </h2>
          <h3 className="desc">{props.description}</h3>
        </div>
        <div className="slide-content">
          <Carousel
            itemsToShow={3}
            breakPoints={breakPoints}
            pagination={false}
            itemPadding={[5, 5]}
          >
            <TeacherItemList
              image={teacher01}
              teacher_name="   "
              description="   "
            ></TeacherItemList>
            <TeacherItemList
              image={teacher02}
              teacher_name="   "
              description="   "
            ></TeacherItemList>
            <TeacherItemList
              image={teacher03}
              teacher_name="   "
              description="   "
            ></TeacherItemList>
            <TeacherItemList
              image={teacher02}
              teacher_name="   "
              description="   "
            ></TeacherItemList>
          </Carousel>
        </div>

        <div className="description-box">
          <div className="text-center">
            <p>{props.text}</p>
          </div>
        </div>
      </div>
    </section>
  );
}
TeacherWrapper.propTypes = {
  title: PropTypes.string.isRequired,
  text: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
};
export default TeacherWrapper;
