import React from "react";
import PropTypes from "prop-types";
import TimeItemLists from "./TimeItemLists";
import Carousel from "react-elastic-carousel";
import "../../../../../assets/public/css/main/_timeWrapper.scss";
function TimeWrapper({ ...props }) {
  const breakPoints = [
    { width: 1, itemsToShow: 1 },
    { width: 550, itemsToShow: 2, itemsToScroll: 1 },
    { width: 850, itemsToShow: 4 },
    { width: 1150, itemsToShow: 4, itemsToScroll: 2 },
    { width: 1450, itemsToShow: 4 },
    { width: 1750, itemsToShow: 4 },
  ];
  return (
    <section className="time-wrapper wrapper">
      <div className="container">
        <div className="title-section">{props.title}</div>

        <div className="time-carousel owl-carousel owl-theme">
          <Carousel
            itemsToShow={4}
            breakPoints={breakPoints}
            pagination={false}
          >
            <TimeItemLists
              title=" "
              text="         "
            ></TimeItemLists>
            <TimeItemLists
              title=" "
              text="         "
            ></TimeItemLists>
            <TimeItemLists
              title=" "
              text="         "
            ></TimeItemLists>
            <TimeItemLists
              title=" "
              text="         "
            ></TimeItemLists>
            <TimeItemLists
              title=" "
              text="         "
            ></TimeItemLists>
          </Carousel>
        </div>

        <div className="text-center">
          <a href={props.link} className="btn btn-primary" data-ripple="ripple">
            {props.link_text}
          </a>
        </div>
      </div>
    </section>
  );
}
TimeWrapper.propTypes = {
  title: PropTypes.string.isRequired,
  link: PropTypes.string.isRequired,
  link_text: PropTypes.string.isRequired,
};
export default TimeWrapper;
