import React, { useState, useEffect } from "react";
import axios from "axios";
import { Form, FormGroup, Input } from "reactstrap";
import { TriggerIsAuthenticated } from "../../../../../services/Recoils";
import { useRecoilState } from "recoil";
import { notificationAlert } from "../../../../../utils/shared";

const Login = ({ props }) => {
  const [phoneNumber, setPhoneNumber] = useState("");
  const [code, setCode] = useState("");
  const [loading, setLoading] = useState(false);
  const [isSMSSent, setIsSMSSent] = useState(false);

  const [triggerIsAuthenticated, setTriggerIsAuthenticated] = useRecoilState(
    TriggerIsAuthenticated
  );

  useEffect(() => {
    return setIsSMSSent(false);
  }, []);

  const handleSubmitLoginStart = (e) => {
    e.preventDefault();

    console.log(process.env.REACT_APP_BACKEND_URL + "api/auth/login-start");

    const submitBtn = e.target.querySelector("#register_submit");
    submitBtn.disabled = true;

    setLoading(true);

    let data = new FormData();
    data.append("phone_number", phoneNumber);

    axios({
      url: process.env.REACT_APP_BACKEND_URL + "api/auth/login-start",
      method: "POST",
      withCredentials: true,
      headers: {
        "Content-Type": "multipart/form-data",
      },
      data,
    }).then((res) => {
      console.log(res.data);
      submitBtn.disabled = false;
      setLoading(false);
      setIsSMSSent(res.data.success);
      notificationAlert(
        res.data.success ? " !" : " !",
        res.data.message,
        res.data.success ? "success" : "error"
      );
    });
  };

  const handleSubmitLoginCode = (e) => {
    e.preventDefault();

    const submitBtn = e.target.querySelector("#register_submit");
    submitBtn.disabled = true;

    setLoading(true);

    let data = new FormData();
    data.append("code", code);

    axios({
      url: process.env.REACT_APP_BACKEND_URL + "api/auth/login-complete",
      method: "POST",
      withCredentials: true,
      headers: {
        "Content-Type": "multipart/form-data",
      },
      data,
    }).then((res) => {
      console.log(res.data);
      submitBtn.disabled = false;
      setLoading(false);

      notificationAlert(
        res.data.success ? " !" : " !",
        res.data.message,
        res.data.success ? "success" : "error"
      );

      setTriggerIsAuthenticated(!triggerIsAuthenticated);
    });
  };

  return (
    <section id="content1" className="tab-content">
      {loading ? (
        "Loading"
      ) : isSMSSent ? (
        <>
          <form onSubmit={handleSubmitLoginCode}>
            <div className="form-group">
              <input
                type="text"
                name="code"
                className="form-control"
                id="code"
                placeholder=""
                onChange={(e) => setCode(e.target.value)}
                required
              />
            </div>
            <div className="form-group">
              <input
                type="submit"
                className="btn btn-primary btn-block"
                id="register_submit"
                value=""
              />
            </div>
            <div className="row">
              <div className="col-md-12">
                <p className="text-right">
                  <a href="#" onClick={(e) => setIsSMSSent(false)}>
                       
                  </a>
                </p>
              </div>
            </div>
          </form>
        </>
      ) : (
        <form onSubmit={handleSubmitLoginStart}>
          <div className="form-group">
            <input
              type="text"
              name="code"
              className="form-control"
              id="code"
              placeholder="     "
              onChange={(e) => setPhoneNumber(e.target.value)}
              required
            />
          </div>

          <div className="form-group">
            <input
              type="submit"
              id="register_submit"
              className="btn btn-primary btn-block"
              value=""
            />
          </div>
        </form>
      )}
    </section>
  );
};
export default Login;
