import React from "react";
import PropTypes from "prop-types";
import MenuItems from "./MenuItems";
import { NavLink } from "react-router-dom";
import "../../../../../assets/public/css/_header.scss";
import { useRecoilState } from "recoil";
import {
  IsAuthenticated,
  UserRole,
  TriggerIsAuthenticated,
} from "../../../../../services/Recoils";

// import { from } from "rxjs";
function Header({ ...props }) {
  const [isAuthenticated, setIsAuthenticated] = useRecoilState(IsAuthenticated);
  const [userRole, setUserRole] = useRecoilState(UserRole);
  const [triggerIsAuthenticated, setTriggerIsAuthenticated] = useRecoilState(
    TriggerIsAuthenticated
  );

  const handelMobileMenu = () => {
    document.querySelector(".menu-aside").classList.add("show");
    document.querySelector(".overal").classList.add("show");
    document.querySelector(".hamburger").classList.toggle("is-active");
  };
  const handelCloseMenu = () => {
    document.querySelector(".menu-aside").classList.remove("show");
    document.querySelector(".overal").classList.remove("show");
    document.querySelector(".hamburger").classList.remove("is-active");
  };

  return (
    <div className="ui-header">
      <div className="overal" onClick={handelCloseMenu}></div>
      <div className="menu-aside">
        <div className="menu-logo">
          <img src={props.logo} alt="Logo" title="Logo" />
        </div>
        <div className="menu-items">
          <ul>
            <MenuItems active="true" link="#" title=" "></MenuItems>
            <MenuItems active="false" link="#" title=" "></MenuItems>
            <MenuItems
              active="false"
              link="#"
              title="  "
            ></MenuItems>
            <MenuItems active="false" link="#" title=" "></MenuItems>
            <MenuItems active="false" link="#" title="  "></MenuItems>
          </ul>
        </div>
      </div>

      <div className="container">
        <div className="row">
          <div className="col-7 col-md-7 col-lg-2">
            <NavLink to="/" className="logo">
              <img src={props.logo} alt={props.title} title={props.title} />
            </NavLink>

            <button
              id="open-menu"
              className="hamburger hamburger--slider"
              type="button"
              onClick={handelMobileMenu}
            >
              <i class="fa fa-bars" aria-hidden="true"></i>
            </button>
          </div>
          <div className="col-lg-8">
            <div className="menu-content">
              <ul>
                <MenuItems active="true" link="#" title=" "></MenuItems>
                <MenuItems
                  active="false"
                  link="#"
                  title=" "
                ></MenuItems>
                <MenuItems
                  active="false"
                  link="#"
                  title="  "
                ></MenuItems>
                <MenuItems active="false" link="#" title=" "></MenuItems>
                <MenuItems
                  active="false"
                  link="#"
                  title="  "
                ></MenuItems>
              </ul>
            </div>
          </div>
          <div className="col-5 col-md-5 col-lg-2">
            <div className="redirect">
              <NavLink
                to={`${
                  isAuthenticated && userRole === "student"
                    ? "/student/userinfo"
                    : "/signin"
                }`}
                target="_blank"
                data-ripple="ripple"
              >
                <i className="fas fa-user"></i> <span>   </span>
              </NavLink>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
Header.propTypes = {
  logo: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  alt: PropTypes.string.isRequired,
};

export default Header;
