import React, { Fragment, useEffect, useState } from "react";
import ProfileDropdown from "./header/ProfileDropdown";
import "../../../../assets/admin/css/Header.scss";
import Axios from "axios";
import logo from "../../../../assets/shared/images/logo.png";

export default function Header() {
  const [name, setName] = useState("");
  const [lastname, setLastname] = useState("");
  const [image, setImage] = useState("");
  const [date, setDate] = useState("");

  // const [currentUserBalance, setCurrnetUserBalance] = useState(0);

  const toggleMenu = () => {
    const element = document.querySelector("aside");
    element.classList.toggle("hide");
  };
  const current_date = () => {
    let moment = require("moment-jalaali");
    moment.loadPersian({ usePersianDigits: true });
    setDate(
      moment(new Date()).format("dddd") +
        ", " +
        moment(new Date()).format("jD jMMM jYYYY")
    );
  };

  useEffect(() => {
    userSigin();
    current_date();
    // getUserBalance();
  });

  const userSigin = () => {
    Axios({
      url:
        process.env.REACT_APP_BACKEND_URL +
        process.env.REACT_APP_GET_USER_INFORMATION,
      withCredentials: true,
      method: "POST",
    }).then((res) => {
      try {
        setName(res.data.data.name);
        setLastname(res.data.data.lastname);
        setImage(res.data.data.user_image);
      } catch (e) {}
    });
  };

  // const getUserBalance = () => {
  //   Axios({
  //     url:
  //       process.env.REACT_APP_BACKEND_URL +
  //       process.env.REACT_APP_STUDENT_CURRENT_BALANCE,
  //     withCredentials: true,
  //     method: "POST",
  //   }).then((res) => {
  //     try {
  //       setCurrnetUserBalance(res.data.data);
  //     } catch (e) {}
  //   });
  // };

  return (
    <header className="header">
      <div className="container-fluid">
        <div className="nav-right">
          <div className="toggle-menu d-inline-block align-middle">
            <button
              className="btn btn-empty"
              onClick={toggleMenu}
              id="toggleMenu"
            >
              <i className="icon-menu"></i>
            </button>
          </div>
          <span className="date">{date}</span>
        </div>

        <a href="/" className="header-logo">
          <img src={logo} alt="Logo" />
        </a>

        <div className="nav-left">
          {/* <span className="px-3">{currentUserBalance} </span> */}

          {/* <Icons /> */}
          <ProfileDropdown
            studentImage={`${process.env.REACT_APP_URL}/uploads/user_image/${image}`}
            studentName={`${name} ${lastname}`}
          />
        </div>
      </div>
    </header>
  );
}
